import React, {useEffect, useState} from "react"
// import "bootstrap/dist/js/bootstrap.bundle"
import "../styles/customTheme.styles.css";
import "../style.css"
import {Collapse} from "react-bootstrap";

import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    useRouteMatch, useLocation,
} from "react-router-dom";
import List from "../components/List";
import Show from "../components/Show";
import Delete from "../components/Delete";
import Edit from "../components/Edit";
import Dashboard from "../components/Dashboard";
import Page404 from "../components/Page404";
import Add from "../components/Add";

const Admin = () => {
    const {path, url} = useRouteMatch();
    const {pathname} = useLocation();

    const [project, setProject] = useState(false)
    const [departments, setDepartments] = useState(false)
    const [socialNetwork, setSocialNetwork] = useState(false)
    const [serviceFile, setServiceFile] = useState(false)
    const [scienceFile, setScienceFile] = useState(false)
    const [scienceContent, setScienceContent] = useState(false)
    const [professor, setProfessor] = useState(false)
    const [faculty, setFaculty] = useState(false)
    const [university, setUniversity] = useState(false)
    const [homeSlider, setHomeSlider] = useState(false)
    const [homeAboutTab, setHomeAboutTab] = useState(false)
    const [homeAboutTile, setHomeAboutTile] = useState(false)
    const [homeAboutCustomer, setHomeAboutCustomer] = useState(false)
    const [home, setHome] = useState(true)
    const [service, setService] = useState(true)
    const [science, setScience] = useState(true)

    useEffect(() => {
        const path_temp1 = pathname.slice(11);
        const path = path_temp1.slice(0, path_temp1.indexOf("/"))
        console.log("path", path);
        switch (path) {
            case "project":
                setProject(true)
                break;
            case "departments":
                setDepartments(true)
                break;
            case "social_network":
                setSocialNetwork(true)
                break;
            case "service_file":
                setServiceFile(true)
                break;
            case "science_file":
                setScienceFile(true)
                break;
            case "science_content":
                setScienceContent(true)
                break;
            case "professor":
                setProfessor(true)
                break;
            case "faculty":
                setFaculty(true)
                break;
            case "university":
                setUniversity(true)
                break;
            case "home_slider":
                setHomeSlider(true)
                break;
            case "home_tab":
                setHomeAboutTab(true)
                break;
            case "home_customer":
                setHomeAboutTile(true)
                break;
            default :
                clearAllActive()
                break;
        }
    }, [])

    const clearAllActive = () => {
        setProject(false)
        setDepartments(false)
        setSocialNetwork(false)
        setServiceFile(false)
        setScienceFile(false)
        setScienceContent(false)
        setProfessor(false)
        setFaculty(false)
        setUniversity(false)
        setHomeSlider(false)
        setHomeAboutTab(false)
        setHomeAboutTile(false)
        setHomeAboutCustomer(false)

    }
    const activeHomeSlider = () => {
        clearAllActive()
        setHomeSlider(true);
    }
    const activeHomeAboutTab = () => {
        clearAllActive()
        setHomeAboutTab(true);
    }
    const activeHomeAboutTile = () => {
        clearAllActive()
        setHomeAboutTile(true);
    }
    const activeHomeAboutCustomer = () => {
        clearAllActive()
        setHomeAboutCustomer(true);
    }
    const activeProject = () => {
        clearAllActive();
        setProject(true);
    }
    const activeDepartments = () => {
        clearAllActive();
        setDepartments(true);
    }
    const activeSocialNetwork = () => {
        clearAllActive();
        setSocialNetwork(true);
    }
    const activeServiceFile = () => {
        clearAllActive();
        setServiceFile(true);
    }
    const activeScienceFile = () => {
        clearAllActive();
        setScienceFile(true);
    }
    const activeScienceContent = () => {
        clearAllActive();
        setScienceContent(true);
    }
    const activeProfessor = () => {
        clearAllActive();
        setProfessor(true);
    }
    const activeFaculty = () => {
        clearAllActive();
        setFaculty(true);
    }
    const activeUniversity = () => {
        clearAllActive();
        setUniversity(true);
    }

    return (
        <Router>
            <div className="container-fluid">
                <div className="row comdit-dashboard">
                    <div className="  col-md-3 col-lg-2   admin-sidebar" style={{background: "black"}}>
                        <ul className="nav flex-column admin-nav">
                            <li className={`nav-item `}>
                                <p
                                    style={{color: "blue #important"}}
                                    className="nav-link-t"
                                    // onClick={() => setService(!service)}
                                    aria-controls="service-collapse-text"
                                    aria-expanded={service}
                                >
                                    صفحه خدمات</p>
                            </li>
                            <Collapse
                                appear={true}
                                timeout={"1000"}
                                in={service}
                            >
                                <div id="service-collapse-text">
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/project/list`}
                                            onClick={activeProject}
                                        >
                                            <p
                                                id="project"
                                                className={`nav-link ${project ? "selected" : ""}`}
                                            >پروژه ها</p>
                                        </Link>
                                    </li>
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/departments/list`}
                                            onClick={activeDepartments}
                                        >
                                            <p
                                                id="departments"
                                                className={`nav-link ${departments ? "selected" : ""}`}
                                            >دپارتمان ها</p>
                                        </Link>
                                    </li>
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/social_network/list`}
                                            onClick={activeSocialNetwork}
                                        >
                                            <p
                                                id="social-network"
                                                className={`nav-link ${socialNetwork ? "selected" : ""}`}
                                            >شبکه های اجتماعی</p>
                                        </Link>
                                    </li>
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/service_file/list`}
                                            onClick={activeServiceFile}
                                        >
                                            <p
                                                id="service-file"
                                                className={`nav-link ${serviceFile ? "selected" : ""}`}
                                            >فایل های رسانه ای</p>
                                        </Link>
                                    </li>
                                </div>
                            </Collapse>

                            <li className={`nav-item`}>
                                <p
                                    style={{color: "blue #important"}}
                                    className="nav-link-t"
                                    // onClick={() => setScience(!science)}
                                    aria-controls="science-collapse-text"
                                    aria-expanded={science}
                                >صفحه علمی</p>
                            </li>
                            <Collapse in={science}>
                                <div id="science-collapse-text">
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/science_file/list`}
                                            onClick={activeScienceFile}
                                        >
                                            <p
                                                className={`nav-link ${scienceFile ? "selected" : ""}`}
                                            >فایل ها</p>
                                        </Link>
                                    </li>
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/science_content/list`}
                                            onClick={activeScienceContent}
                                        >
                                            <p
                                                className={`nav-link ${scienceContent ? "selected" : ""}`}
                                            >مطالب</p>
                                        </Link>
                                    </li>
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/professor/list`}
                                            onClick={activeProfessor}
                                        >
                                            <p
                                                className={`nav-link ${professor ? "selected" : ""}`}
                                            >اساتید</p>
                                        </Link>
                                    </li>
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/faculty/list`}
                                            onClick={activeFaculty}
                                        >
                                            <p
                                                className={`nav-link ${faculty ? "selected" : ""}`}
                                            >دانشکده ها</p>
                                        </Link>
                                    </li>
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/university/list`}
                                            onClick={activeUniversity}
                                        >
                                            <p
                                                className={`nav-link ${university ? "selected" : ""}`}
                                            >دانشگاه ها</p>
                                        </Link>
                                    </li>
                                </div>
                            </Collapse>
                            <li className={`nav-item`}>
                                <p
                                    style={{color: "blue #important"}}
                                    className="nav-link-t"
                                    onClick={() => setHome(!home)}
                                    aria-controls="home-collapse-text"
                                    aria-expanded={home}
                                >صفحه اصلی</p>
                            </li>
                            <Collapse in={home}>
                                <div id="home-collapse-text">
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/home_slider/list`}
                                            onClick={activeHomeSlider}
                                        >
                                            <p
                                                className={`nav-link ${homeSlider ? "selected" : ""}`}
                                            >اسلایدر</p>
                                        </Link>
                                    </li>
                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/home_tab/list`}
                                            onClick={activeHomeAboutTab}
                                        >
                                            <p
                                                className={`nav-link ${homeAboutTab ? "selected" : ""}`}
                                            >درباره ما</p>
                                        </Link>
                                    </li>

                                    <li className="nav-item sub">
                                        <Link
                                            to={`/dashboard/home_customer/list`}
                                            onClick={activeHomeAboutCustomer}
                                        >
                                            <p
                                                className={`nav-link ${homeAboutCustomer ? "selected" : ""}`}
                                            >درباره ما مشتریان</p>
                                        </Link>
                                    </li>

                                </div>
                            </Collapse>
                        </ul>
                    </div>
                    <div className="col-md-9 col-lg-10">
                        <Switch>
                            <Route path={`${path}/:model/list`}>
                                <List prefix={`${path}`}/>
                            </Route>
                            <Route path={`${path}/:model/add`}>
                                <Add prefix={`${path}`}/>
                            </Route>
                            <Route path={`${path}/:model/:id/show`}>
                                <Show/>
                            </Route>
                            <Route path={`${path}/:model/:id/delete`}>
                                <Delete prefix={`${path}`}/>
                            </Route>
                            <Route path={`${path}/:model/:id/edit`}>
                                <Edit/>
                            </Route>
                            <Route path={`${path}/*`}>
                                <Page404/>
                            </Route>
                            <Route exact path={`${path}/`}>
                                <Dashboard/>
                            </Route>
                        </Switch>
                    </div>
                </div>
            </div>
        </Router>
    )
}
export default Admin;